.container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: calc(100vh - 80px);
}

.form {
  background: #222;
  padding: 2rem;
  border-radius: 8px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.5);
  width: 100%;
  max-width: 500px;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.form h2 {
  color: #e50914;
  text-align: center;
  margin-bottom: 1rem;
}

.inputGroup {
  display: flex;
  flex-direction: column;
}

.inputGroup label {
  margin-bottom: 0.5rem;
  color: #ccc;
}

.input {
  padding: 0.8rem;
  border-radius: 4px;
  border: 1px solid #444;
  background: #333;
  color: white;
  font-size: 1rem;
}

.input:focus {
  outline: none;
  border-color: #e50914;
}

.button {
  padding: 0.8rem;
  border: none;
  border-radius: 4px;
  background-color: #e50914;
  color: white;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s;
}

.button:hover {
  background-color: #f40612;
}